const PORTFOLIO_POPUP = 'PORTFOLIO_POPUP';
const ABOUT_TABS = 'ABOUT_TABS';
const BURGER = 'BURGER'
const LOAD = 'LOAD'
const DARKMODE = 'DARKMODE'
const initState = {
  portfolioPopup: '',
  aboutTabs: 'edu',
  burger: false,
  darkmode: false
};
const StateFunc = (state = initState, action) => {
  switch (action.type) {
    case PORTFOLIO_POPUP: {
      return {
        ...state,
        portfolioPopup: action.portfolioPopup,
      };
    }
    case ABOUT_TABS: {
      return {
        ...state,
        aboutTabs: action.aboutTabs,
      };
    }
    case BURGER: {
      return {
        ...state,
        burger: action.burger
      }
    }
    case DARKMODE: {
      return {
        ...state, darkmode: action.darkmode
      }
    }
    default:
      return state;
  }
};
export default StateFunc;

export const togglePortfolioPopup = (prop) => {
  return {
    type: PORTFOLIO_POPUP,
    portfolioPopup: prop,
  };
};

export const setAboutTabs = (prop) => {
  return {
    type: ABOUT_TABS,
    aboutTabs: prop,
  };
};

export const setBurger = (prop) => {
  return {
    type: BURGER,
    burger: prop
  }
}

export const setLoad = (prop) => {
  return {
    type: LOAD,
    load: prop
  }
}

export const setDarkmode = (prop) => {
  return {
    type: DARKMODE,
    darkmode: prop
  }
}