import React from 'react';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { setBurger } from '../redux/reducers/mainReducer';

const Navbar = () => {
  const burger = useSelector((s) => s.mainReducer.burger);
  const dispatch = useDispatch();
  const linkHandler = (e) => {
    dispatch(setBurger(false));
    if (burger) {
      document.querySelector('.burger').classList.remove('active');
      document.querySelector('.burger').classList.add('hide');
      document.querySelector('.toggle').classList.add('hide');
      setTimeout(() => {
        document.querySelector('section').classList.add('active');
        document.querySelector('section').classList.remove('fade-out');
        document.querySelector('.toggle').classList.remove('hide');
        document.querySelector('.burger').classList.remove('hide');
      }, 500);
    } else {
      document.querySelector('section').classList.add('fade-out');
    }
  };
  return (
    <nav className="navbar">
      <div className="navbar-collapse">
        <ul className="navbar-nav">
          <li className="nav-item">
            <Link
              to="/"
              className="nav-link link-item"
              datahash="#home"
              data-content="Home"
              onClick={linkHandler}
            >
              Home
            </Link>
          </li>
          <li className="nav-item">
            <Link
              to="/about"
              className="nav-link link-item"
              datahash="#about"
              data-content="about"
              onClick={linkHandler}
            >
              About
            </Link>
          </li>
          <li className="nav-item">
            <Link
              to="/portfolio"
              className="nav-link link-item"
              datahash="#portfolio"
              data-content="Portfolio"
              onClick={linkHandler}
            >
              Portfolio
            </Link>
          </li>
          <li className="nav-item">
            <Link
              to="/contact"
              className="nav-link link-item"
              datahash="#contact"
              data-content="Contact"
              onClick={linkHandler}
            >
              Contact
            </Link>
          </li>
        </ul>
      </div>
    </nav>
  );
};

export default Navbar;
