import React from 'react';

import { Link } from 'react-router-dom';
const Portfolio = ({ portfolioData }) => {
  const portfolioHandler = () => {
    setTimeout(() => {
      document.querySelector('.burger').classList.add('hide');
      document.querySelector('.toggle').classList.add('hide');
      document.querySelector('.portfolio-popup').classList.add('open');
    }, 1);
  };

  return (
    <section className="portfolio padding" id="portfolio">
      <div className="container">
        <div className="row">
          <div className="title">
            <h2>recent work</h2>
          </div>
        </div>
        <div className="row">
          {portfolioData.map((el) => {
            console.log(portfolioData);
            return (
              <div
                className="portfolio-item col-12 col-md-6 col-lg-4"
                key={el.id}
              >
                <Link to={`/portfolio/${el.name}`}>
                  <img
                    className="portfolio-item-img"
                    src={el.img}
                    alt={el.title}
                    onClick={portfolioHandler}
                  />
                </Link>
                <h3 className="portfolio-item-title">{el.title}</h3>
                <a
                  href={el.link}
                  target="_blank"
                  className="btn"
                  rel="noreferrer"
                >
                  View online
                </a>
                <div className="portfolio-item-details">
                  <div className="description">
                    <p>
                      Lorem ipsum dolor sit amet, consectetur adipisicing elit.
                      Quasi pariatur officiis modi debitis error animi corporis
                      nostrum! Fugiat corporis porro mollitia accusamus,
                      aspernatur saepe. Facilis deserunt ullam eligendi
                      adipisci. Maxime laboriosam expedita fuga dignissimos,
                      perspiciatis nobis voluptatibus dolores repellat
                      asperiores!
                    </p>
                  </div>
                  <div className="general-info">
                    <ul>
                      <li>
                        Created - <span>{el.createDate}</span>
                      </li>
                      <li>
                        Technologies Used - <span>{el.technologies}</span>
                      </li>
                      <li>
                        View Online{' '}
                        <span>
                          <a href={el.link} target="_blank" rel="noreferrer">
                            {el.link}
                          </a>
                        </span>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
};

export default Portfolio;
