import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import About from './components/About';
import BgCircles from './components/BgCircles';
import Contact from './components/Contact';
import Header from './components/Header';
import Home from './components/Home';
import Overlay from './components/Overlay';
import PageLoader from './components/PageLoader';
import Portfolio from './components/Portfolio';
import PortfolioPopup from './components/PortfolioPopup';
import './style/style.min.css';
import initPortfolioData from './components/portfolioData';
import { useState } from 'react';
window.addEventListener('load', () => {
  document.querySelector('.main').classList.remove('hidden');
  if (document.querySelector('section')) {
    document.querySelector('section').classList.add('active');
  }
  document.querySelector('.page-loader').classList.add('fade-out');
  setTimeout(() => {
    document.querySelector('.page-loader').style.display = 'none';
  }, 600);
});

const App = () => {
  const [portfolioData, setPortfolioData] = useState(initPortfolioData)
  return (
    <BrowserRouter>
      <div className="main">
        <Header />
        <Switch>
          <Route exact path="/" component={() => <Home />} />
          <Route exact path="/about" component={() => <About />} />
          <Route exact path="/portfolio" component={() => <Portfolio portfolioData={portfolioData} />} />
          <Route exact path="/portfolio/:projectName" component={() => <PortfolioPopup portfolioData={portfolioData} />} />
          <Route exact path="/contact" component={() => <Contact />} />
        </Switch>
      </div>
      <PageLoader />
      <BgCircles />
      <Overlay />
    </BrowserRouter>
  );
};

export default App;
