import React from 'react';
import {gsap} from 'gsap'

const Contact = () => {
  setTimeout(() => {
    const form = document.getElementById('contact-form');
    if (form) {
      async function handleSubmit(event) {
        event.preventDefault();
        const status = document.getElementById('form-status');
        const btn = document.getElementById('contact-submit');
        const data = new FormData(event.target);
        fetch(event.target.action, {
          method: form.method,
          body: data,
          headers: {
            Accept: 'application/json',
          },
        })
          .then((response) => {
            status.textContent = 'Thanks for your submission!';
            btn.classList.remove('active');
            form.reset();
          })
          .catch((error) => {
            status.innerHTML = 'Oops! There was a problem submitting your form';
          });
        setTimeout(() => {
          status.textContent = '';
        }, 5000);
      }
      form.addEventListener('submit', handleSubmit);
    }
    document.querySelectorAll('.button').forEach((button) => {
      let getVar = (variable) =>
        getComputedStyle(button).getPropertyValue(variable);
      button.addEventListener('click', (e) => {
        setTimeout(() => {
          if (!button.classList.contains('active')) {
            button.classList.add('active');
            gsap.to(button, {
              keyframes: [
                {
                  '--left-wing-first-x': 50,
                  '--left-wing-first-y': 100,
                  '--right-wing-second-x': 50,
                  '--right-wing-second-y': 100,
                  duration: 0.2,
                  onComplete() {
                    gsap.set(button, {
                      '--left-wing-first-y': 0,
                      '--left-wing-second-x': 40,
                      '--left-wing-second-y': 100,
                      '--left-wing-third-x': 0,
                      '--left-wing-third-y': 100,
                      '--left-body-third-x': 40,
                      '--right-wing-first-x': 50,
                      '--right-wing-first-y': 0,
                      '--right-wing-second-x': 60,
                      '--right-wing-second-y': 100,
                      '--right-wing-third-x': 100,
                      '--right-wing-third-y': 100,
                      '--right-body-third-x': 60,
                    });
                  },
                },
                {
                  '--left-wing-third-x': 20,
                  '--left-wing-third-y': 90,
                  '--left-wing-second-y': 90,
                  '--left-body-third-y': 90,
                  '--right-wing-third-x': 80,
                  '--right-wing-third-y': 90,
                  '--right-body-third-y': 90,
                  '--right-wing-second-y': 90,
                  duration: 0.2,
                },
                {
                  '--rotate': 50,
                  '--left-wing-third-y': 95,
                  '--left-wing-third-x': 27,
                  '--right-body-third-x': 45,
                  '--right-wing-second-x': 45,
                  '--right-wing-third-x': 60,
                  '--right-wing-third-y': 83,
                  duration: 0.25,
                },
                {
                  '--rotate': 55,
                  '--plane-x': -8,
                  '--plane-y': 24,
                  duration: 0.2,
                },
                {
                  '--rotate': 40,
                  '--plane-x': 45,
                  '--plane-y': -180,
                  '--plane-opacity': 0,
                  duration: 0.3,
                  onComplete() {
                    setTimeout(() => {
                      button.removeAttribute('style');
                      gsap.fromTo(
                        button,
                        {
                          opacity: 0,
                          y: -8,
                        },
                        {
                          opacity: 1,
                          y: 0,
                          clearProps: true,
                          duration: 0.3,
                          onComplete() {
                            button.classList.add('active');
                          },
                        }
                      );
                    }, 2000);
                  },
                },
              ],
            });
    
            gsap.to(button, {
              keyframes: [
                {
                  '--text-opacity': 0,
                  '--box-shadow-20': 0,
                  '--border-top': 0,
                  '--border-left': 0,
                  '--border-radius': 0,
                  '--plane-opacity': 1,
                  '--background-color': 0,
                  '--bg-hover': 0,
                  '--left-wing-background': getVar('--primary-darkest'),
                  '--right-wing-background': getVar('--primary-darkest'),
                  duration: 0.1,
                },
                {
                  '--left-wing-background': getVar('--primary'),
                  '--right-wing-background': getVar('--primary'),
                  duration: 0.1,
                },
                {
                  '--left-body-background': getVar('--primary-dark'),
                  '--right-body-background': getVar('--primary-darkest'),
                  duration: 0.4,
                },
                {
                  '--success-opacity': 1,
                  '--success-scale': 1,
                  duration: 0.55,
                  delay: 0.25,
                },
              ],
            });
          }
        }, 1000);
      });
    });
  }, 1);

  return (
    <section className="contact padding" id="contact">
      <div className="container">
        <div className="row">
          <div className="title">
            <h2>contact me</h2>
          </div>
        </div>
        <div className="row">
          <div className="contact-form col-12 col-md-6">
            <form
              action="https://formspree.io/f/mvodklzw"
              method="POST"
              id="contact-form"
            >
              <div className="row">
                <label className="label">
                  <input
                    className="input"
                    type="text"
                    autoComplete="off"
                    name="Name"
                    required
                  />
                  <span className="text">Name</span>
                  <span className="line"></span>
                </label>
                <label className="label">
                  <input
                    className="input"
                    type="text"
                    autoComplete="off"
                    name="Email"
                    required
                  />
                  <span className="text">email</span>
                  <span className="line"></span>
                </label>
                <label className="label label-textarea">
                  <textarea
                    className="input textarea"
                    name="Message"
                    required
                  ></textarea>
                  <span className="text">message</span>
                  <span className="line"></span>
                </label>
                <div>
                  <button
                    type="submit"
                    className="button active"
                    id="contact-submit"
                  >
                    <span className="default">send message</span>
                    <span className="success" id="form-status"></span>
                    <div className="left"></div>
                    <div className="right"></div>
                  </button>
                </div>
              </div>
            </form>
          </div>
          <div className="contact-info col-12 col-md-6">
            <div className="contact-info-item">
              <h3>email</h3>
              <p>amantay.sv@gmail.com</p>
            </div>
            <div className="contact-info-item">
              <h3>phone</h3>
              <p>+996556083082</p>
            </div>
            <div className="contact-info-item">
              <h3>my social</h3>
              <ul className="social-nav">
                <li className="social-item">
                  <a
                    href="https://www.linkedin.com/in/amantay-suranaliev-7762a1203/"
                    target="_blank"
                    className="social-link"
                    rel="noreferrer"
                  >
                    <i className="fab fa-linkedin-in"></i>
                  </a>
                </li>
                <li className="social-item">
                  <a
                    href="https://wa.me/996556083082"
                    target="_blank"
                    className="social-link"
                    rel="noreferrer"
                  >
                    <i className="fab fa-whatsapp"></i>
                  </a>
                </li>
                <li className="social-item">
                  <button
                    href="#"
                    target="_blank"
                    rel="noreferrer"
                    className="social-link"
                  >
                    <i className="fab fa-facebook-f"></i>
                  </button>
                </li>
                <li className="social-item">
                  <button
                    href="#"
                    target="_blank"
                    rel="noreferrer"
                    className="social-link"
                  >
                    <i className="fab fa-twitter"></i>
                  </button>
                </li>
                <li className="social-item">
                  <button
                    href="#"
                    target="_blank"
                    rel="noreferrer"
                    className="social-link"
                  >
                    <i className="fab fa-instagram"></i>
                  </button>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Contact;
