import { useDispatch, useSelector } from 'react-redux';
import { setBurger } from '../redux/reducers/mainReducer';
const PortfolioPopup = ({ portfolioData }) => {
  const { projectName } = useParams();
  const dispatch = useDispatch();
  const closePopup = () => {
    document.querySelector('.portfolio-popup').classList.remove('open');
    dispatch(setBurger(false));
    document.querySelector('.burger').classList.add('hide');
    document.querySelector('.toggle').classList.add('hide');
    setTimeout(() => {
      document.querySelector('section').classList.add('active');
      document.querySelector('.toggle').classList.remove('hide');
      document.querySelector('.burger').classList.remove('hide');
    }, 500);
  };
  return portfolioData.map((el) => {
    if (el.name === projectName) {
      return (
        <div className="portfolio-popup">
          <div className="portfolio-popup-inner">
            <div className="portfolio-popup-content border-40">
              <div className="portfolio-popup-header">
                <Link
                  to="/portfolio"
                  className="btn portfolio-popup-close"
                  datahash="#portfolio"
                  onClick={closePopup}
                  timeout={300}
                >
                  <i className="fas fa-times"></i>
                </Link>

                <div className="portfolio-popup-thumbnail">
                  <img src={el.img} alt="" />
                </div>
                <h3>{projectName}</h3>
              </div>
              <div className="portfolio-popup-body">
                <div className="description">
                  <p>
                    Lorem ipsum dolor sit amet, consectetur adipisicing elit.
                    Quasi pariatur officiis modi debitis error animi corporis
                    nostrum! Fugiat corporis porro mollitia accusamus,
                    aspernatur saepe. Facilis deserunt ullam eligendi adipisci.
                    Maxime laboriosam expedita fuga dignissimos, perspiciatis
                    nobis voluptatibus dolores repellat asperiores!
                  </p>
                </div>
                <div className="general-info">
                  <ul>
                    <li>
                      Created - <span>{el.createDate}</span>
                    </li>
                    <li>
                      Technologies Used - <span>{el.technologies}</span>
                    </li>
                    <li>
                      View Online{' '}
                      <span>
                        <a href={el.link} target="_blank" rel="noreferrer">
                          {el.link}
                        </a>
                      </span>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      );
    }
  });
};

export default PortfolioPopup;
