import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setBurger } from '../redux/reducers/mainReducer';

const Burger = () => {
  const burger = useSelector((s) => s.mainReducer.burger);
  const dispatch = useDispatch();
  const burgerHandler = () => {
    burger ? dispatch(setBurger(false)) : dispatch(setBurger(true));
    if(burger) {
      document.querySelector('.burger').classList.remove('active');
      setTimeout(() => {
          document.querySelector('section').classList.remove('fade-out');
        }, 500)
    } else {
      document.querySelector('section').classList.add('fade-out');
      document.querySelector('.burger').classList.add('active');
    }
  };
  return (
    <button
      type="button"
      onClick={burgerHandler}
      id="burger"
      className={`burger`}
    >
      <span></span>
    </button>
  );
};

export default Burger;
